html
	height: 100%
	box-sizing: border-box

*, *::before, *::after
	margin: 0
	padding: 0
	outline: 0
	border: 0
	box-sizing: inherit

ul
	list-style: none

button, label, select, input[type="submit"]
	cursor: pointer

input, textarea, select, button
	font-family: inherit
	font-size: inherit
	font-weight: inherit
	color: inherit

img
	max-width: 100%
	height: auto

input[type="radio"], input[type="checkbox"]
	position: absolute
	opacity: 0
	top: 0
	left: 0

a
	text-decoration: none

a,
a::before,
a::after,
button,
button::before,
button::after,
.slick-dots li,
input[type="submit"]
	transition: all .5s ease

.menu-toggle
	margin: 0
	position: absolute
	top: 50%
	transform: translateY(-50%)
	right: 7px
	display: none
	align-items: center
	justify-content: center
	width: 36px
	height: 36px
	cursor: pointer
	.icon-toggle
		width: 16px
		height: 10px
		position: relative
		display: flex
		align-items: center
		span
			height: 2px
			background: #2D2D2D
			position: absolute
			width: 100%
			border-radius: 2px
			transition: .3s
			left: 0
			&:first-child
				top: 0
			&:last-child
				bottom: 0
		&.active
			span
				&:first-child
					top: auto
					transform: rotate(45deg)
				&:last-child
					bottom: auto
					transform: rotate(-45deg)
				&:nth-child(2)
					opacity: 0
